module.exports = "<table style=\"display: table; margin-bottom: 1.25rem\"><thead><tr style=\"line-height: 1.5\"><th style=\"width: 10%\">Prop Name</th><th style=\"width: 25%\">Type</th><th style=\"width: 10%\">Is Required</th><th style=\"width: 10%\">Default Value</th><th style=\"width: 45%\">Description</th></tr></thead><tbody><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">children</td><td style=\"padding-bottom: 1.8rem\">element</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">undefined</td><td style=\"padding-bottom: 1.8rem\">Displays a single terra <code style=\"white-space: pre-wrap\">Collapsible Menu View</code> (<em>Not provided by <code>Action Header</code></em>) child element on the right end of the header.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">level</td><td style=\"padding-bottom: 1.8rem\">enum</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">1</td><td style=\"padding-bottom: 1.8rem\">Optionally sets the heading level. One of <code style=\"white-space: pre-wrap\">1</code>, <code>2</code>, <code>3</code>, <code>4</code>, <code>5</code>, <code>6</code>. Default <code>level=1</code>.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">onClose</td><td style=\"padding-bottom: 1.8rem\">func</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">undefined</td><td style=\"padding-bottom: 1.8rem\">Callback function for when the close button is clicked.\nOn small viewports, this will be triggered by a back button if onBack is not set.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">onBack</td><td style=\"padding-bottom: 1.8rem\">func</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">undefined</td><td style=\"padding-bottom: 1.8rem\">Callback function for when the back button is clicked. The back button will not display if this is not set.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">onMaximize</td><td style=\"padding-bottom: 1.8rem\">func</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">undefined</td><td style=\"padding-bottom: 1.8rem\">Callback function for when the expand button is clicked.\nThe expand button will not display if this is not set or on small viewports.\nOnly the expand button will be rendered if onMaximize and onMinimize are set.<em>Note: If <code style=\"white-space: pre-wrap\">onBack</code> is set, the maximize button will not appear and a custom maximize button must be provided\nas a child inside a <code>Collapsible Menu View</code>.</em></td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">onMinimize</td><td style=\"padding-bottom: 1.8rem\">func</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">undefined</td><td style=\"padding-bottom: 1.8rem\">Callback function for when the minimize button is clicked.\nThe minimize button will not display if this is not set or on small viewports.\nOnly the expand button will be rendered if both onMaximize and onMinimize are set.<em>Note: If <code style=\"white-space: pre-wrap\">onBack</code> is set, the minimize button will not appear and a custom minimize button must be provided\nas a child inside a <code>Collapsible Menu View</code>.</em></td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">onNext</td><td style=\"padding-bottom: 1.8rem\">func</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">undefined</td><td style=\"padding-bottom: 1.8rem\">Callback function for when the next button is clicked. The previous-next button group will display if either this or onPrevious is set but the button for the one not set will be disabled.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">onPrevious</td><td style=\"padding-bottom: 1.8rem\">func</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">undefined</td><td style=\"padding-bottom: 1.8rem\">Callback function for when the previous button is clicked. The previous-next button group will display if either this or onNext is set but the button for the one not set will be disabled.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">title</td><td style=\"padding-bottom: 1.8rem\">string</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">undefined</td><td style=\"padding-bottom: 1.8rem\">Text to be displayed as the title in the header bar.</td></tr></tbody></table>"